"use client"

import { useState, useEffect } from "react"
import { Plus, Trash2, CheckCircle2, Circle } from "lucide-react"

interface Todo {
  id: string
  title: string
  description: string
  completed: boolean
  day: string
  priority: "high" | "medium" | "low"
}

export default function WeeklyTodoPage() {
  const [todos, setTodos] = useState<Todo[]>([])
  const [newTitle, setNewTitle] = useState("")
  const [newDescription, setNewDescription] = useState("")
  const [selectedDay, setSelectedDay] = useState("Monday")
  const [selectedPriority, setSelectedPriority] = useState<"high" | "medium" | "low">("medium")
  const [filter, setFilter] = useState<"all" | "active" | "completed">("all")

  const days = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday", "Sunday"]

  // Load todos
  useEffect(() => {
    const stored = localStorage.getItem("weeklyTodos")
    if (stored) {
      setTodos(JSON.parse(stored))
    }
  }, [])

  // Save todos
  useEffect(() => {
    localStorage.setItem("weeklyTodos", JSON.stringify(todos))
  }, [todos])

  const handleAddTodo = () => {
    if (!newTitle.trim()) return
    const todo: Todo = {
      id: Date.now().toString(),
      title: newTitle,
      description: newDescription,
      completed: false,
      day: selectedDay,
      priority: selectedPriority,
    }
    setTodos([...todos, todo])
    setNewTitle("")
    setNewDescription("")
  }

  const handleToggleTodo = (id: string) => {
    setTodos(todos.map((todo) => (todo.id === id ? { ...todo, completed: !todo.completed } : todo)))
  }

  const handleDeleteTodo = (id: string) => {
    setTodos(todos.filter((todo) => todo.id !== id))
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "high":
        return "bg-red-100/50 text-red-700 border-red-200"
      case "medium":
        return "bg-yellow-100/50 text-yellow-700 border-yellow-200"
      case "low":
        return "bg-green-100/50 text-green-700 border-green-200"
      default:
        return ""
    }
  }

  const filteredTodos = todos.filter((todo) => {
    if (filter === "active") return !todo.completed
    if (filter === "completed") return todo.completed
    return true
  })

  const stats = {
    total: todos.length,
    completed: todos.filter((t) => t.completed).length,
    active: todos.filter((t) => !t.completed).length,
  }

  return (
    <div className="p-4 lg:p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-foreground mb-2">Weekly To-Do List</h1>
        <p className="text-muted-foreground">Organize your tasks by day and priority</p>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-3 gap-4 mb-8">
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Total Tasks</p>
          <p className="text-2xl font-bold text-foreground">{stats.total}</p>
        </div>
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Completed</p>
          <p className="text-2xl font-bold text-green-600">{stats.completed}</p>
        </div>
        <div className="bg-card rounded-lg p-4 border border-border">
          <p className="text-sm text-muted-foreground mb-1">Active</p>
          <p className="text-2xl font-bold text-secondary">{stats.active}</p>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Add Todo Form */}
        <div className="bg-card rounded-xl shadow-md p-6 border border-border lg:col-span-1 h-fit">
          <h2 className="text-lg font-bold text-foreground mb-4">Add New Task</h2>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Title</label>
              <input
                type="text"
                value={newTitle}
                onChange={(e) => setNewTitle(e.target.value)}
                placeholder="Task title"
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Description</label>
              <textarea
                value={newDescription}
                onChange={(e) => setNewDescription(e.target.value)}
                placeholder="Add details"
                rows={3}
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Day</label>
              <select
                value={selectedDay}
                onChange={(e) => setSelectedDay(e.target.value)}
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              >
                {days.map((day) => (
                  <option key={day} value={day}>
                    {day}
                  </option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-sm font-medium text-foreground mb-2">Priority</label>
              <select
                value={selectedPriority}
                onChange={(e) => setSelectedPriority(e.target.value as any)}
                className="w-full px-4 py-2 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
              </select>
            </div>
            <button
              onClick={handleAddTodo}
              className="w-full bg-primary hover:bg-primary/90 text-primary-foreground px-4 py-2 rounded-lg font-medium transition-colors flex items-center justify-center gap-2"
            >
              <Plus size={20} />
              Add Task
            </button>
          </div>
        </div>

        {/* Tasks List */}
        <div className="lg:col-span-2 space-y-4">
          {/* Filter Buttons */}
          <div className="flex gap-2 mb-4">
            {["all", "active", "completed"].map((f) => (
              <button
                key={f}
                onClick={() => setFilter(f as any)}
                className={`px-4 py-2 rounded-lg font-medium transition-colors ${
                  filter === f
                    ? "bg-primary text-primary-foreground"
                    : "bg-muted text-muted-foreground hover:bg-muted/80"
                }`}
              >
                {f.charAt(0).toUpperCase() + f.slice(1)}
              </button>
            ))}
          </div>

          {/* Tasks by Day */}
          {days.map((day) => {
            const dayTodos = filteredTodos.filter((todo) => todo.day === day)
            if (dayTodos.length === 0) return null

            return (
              <div key={day} className="bg-card rounded-xl shadow-md p-6 border border-border">
                <h3 className="text-lg font-bold text-foreground mb-4">{day}</h3>
                <div className="space-y-3">
                  {dayTodos.map((todo) => (
                    <div
                      key={todo.id}
                      className={`flex items-start gap-3 p-3 rounded-lg border transition-all ${
                        todo.completed
                          ? "bg-muted/30 border-border/50 opacity-60"
                          : "bg-background border-border hover:border-secondary"
                      }`}
                    >
                      <button
                        onClick={() => handleToggleTodo(todo.id)}
                        className="mt-1 text-secondary hover:text-secondary/80"
                      >
                        {todo.completed ? <CheckCircle2 size={20} className="text-green-600" /> : <Circle size={20} />}
                      </button>
                      <div className="flex-1">
                        <p
                          className={`font-medium ${
                            todo.completed ? "line-through text-muted-foreground" : "text-foreground"
                          }`}
                        >
                          {todo.title}
                        </p>
                        {todo.description && <p className="text-sm text-muted-foreground mt-1">{todo.description}</p>}
                        <div className="flex gap-2 mt-2">
                          <span
                            className={`text-xs font-medium px-2 py-1 rounded border ${getPriorityColor(
                              todo.priority,
                            )}`}
                          >
                            {todo.priority}
                          </span>
                        </div>
                      </div>
                      <button
                        onClick={() => handleDeleteTodo(todo.id)}
                        className="text-destructive hover:text-destructive/80 mt-1"
                      >
                        <Trash2 size={18} />
                      </button>
                    </div>
                  ))}
                </div>
              </div>
            )
          })}

          {filteredTodos.length === 0 && (
            <div className="text-center py-12 bg-card rounded-xl border border-border">
              <p className="text-muted-foreground">No tasks found. Create one to get started!</p>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
